
.search-toggle {
    background-color: #f9b342;
    border: 3px solid #fff;
    border-radius: 5px;
    font-weight: 400;
    color: #ffffff;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    padding: 1rem 1.5rem;
    transform: translate(25px, -25px);
    word-spacing: 0.9rem;
    width: 150px;
  }
  
  .search-toggle:hover {
    background-color: #f9b342;
    border-color: #fff;
  }
  
  .search-toggle:active {
    background-color: #f9b342;
    border-color: #fff;
  }
  
  .search-expanded {
    display: none;
  }
  
  @media (max-width: 766px) {
    .search-toggle {
      display: none;
    }
    .search-expanded {
      margin-top: -7px;
      display: block;
      background-color: #f9b342;
      padding: 20px;
    }
  }
  
  .search-expanded-button {
    background-color: #fff;
    width: 100%;
    cursor: pointer;
    padding: 15px;
    color: #f9b342;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-content: center;
  }
  
  .search-expanded-label {
    margin-left: 1rem;
  }
  