.wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 1300px;
    padding: 0px 20px;
    margin: 2rem auto;
  }
  
  .main {
    background-color: #fff;
    margin-bottom: 2rem;
    margin-right: 2rem;
    width: 69.8%;
  }
  
  @media only screen and (max-width: 800px) {
    .wrapper {
      display: flex;
      flex-direction: column;
    }
  
    .main {
      width: 100%;
    }
  }

.site-description {
    text-align: center;
    color: #f9b342;
  }
  